<template>
	<div class="payPhoneConfirm">
		<div class="payPhoneConfirm_head">
			<i class="icon_arrow_left_black" @click="goBack"></i>
			<p class="payPhoneConfirm_head_title">确认下单</p>
		</div>
		<div class="payPhoneConfirm_main">
			<div class="payPhoneConfirm_box">
				<img :src="payData.imgPath" alt="" class="payPhoneConfirm_box_img">
				<div>
					<p class="payPhoneConfirm_box_text1">联通、电信、移动{{ payData.amount }}元话费充值</p>
					<p class="payPhoneConfirm_box_text2">充值号码：{{ phone }}</p>
				</div>
			</div>
			<div class="payPhoneConfirm_box flex">
				<p class="payPhoneConfirm_box_text3">优惠折扣：</p>
				<p class="payPhoneConfirm_box_text4">{{ '-¥'+(payData.amount-payData.payAmount)+'('+ discount +'折)' }}</p>
			</div>
			<div class="payPhoneConfirm_box flex">
				<p class="payPhoneConfirm_box_text3">福利值抵扣：</p>
				<p class="payPhoneConfirm_box_text4" @click="selectBond">{{  $store.state.bond.id != '' ?'- ¥'+$store.state.bond.amount+'元':'选择抵扣券' }}</p>
			</div>
			<div class="payPhoneConfirm_box flex">
				<p class="payPhoneConfirm_box_text3">支付方式：</p>
				<p class="payPhoneConfirm_box_text4">
					<i class="icon_wechat"></i>
					<span>微信支付</span>
				</p>
			</div>
			<div class="payPhoneConfirm_box flex">
				<p class="payPhoneConfirm_box_text3">实际支付：</p>
				<p class="payPhoneConfirm_box_text4 color_ff588c">{{ (payData.payAmount-$store.state.bond.amount) > 0 ? '¥'+(payData.payAmount-$store.state.bond.amount) : '¥0' }}</p>
			</div>
			<div class="payPhoneConfirm_box flex">
				<p class="payPhoneConfirm_box_text3">福利值赠送：</p>
				<p class="payPhoneConfirm_box_text4 color_ff588c">{{ Math.floor((payData.payAmount-$store.state.bond.amount)/10)*cashBack }}</p>
			</div>
			<p class="btn_pay" @click="pay">确认支付</p>
			<van-checkbox v-model="checked" checked-color="#0cd4d2" icon-size="0.5rem"><span class="checkedText"></span>支付既代表同意【下单说明】</van-checkbox>
			<p class="description">【下单说明】欠费停机可充，写好转网及虚商号码不可充，0到72小时内到账，急用勿充。有可能会分批到账，到账后可登录运营商官方掌厅查询到账情况，充值前请仔细核对充值号码和充值金额后再下单，充错后无法退换。本充值服务不提供发票，下单代表同意该【下单说明】</p>
		</div>
		
	</div>
</template>

<script scoped>
	import dsbridge from '../assets/js/dsbridge.js';
	var dataSS = {
		actualAmount: 0,
		payAmount: 0,
		appVersion: dsBridge.call("lyh5x.device.appVersion"),
		battery: dsBridge.call("lyh5x.device.battery"),
		brand: dsBridge.call("lyh5x.device.brand"),
		connectionType: dsBridge.call("lyh5x.device.connectionType"),
		deviceType: dsBridge.call("lyh5x.device.deviceType"),
		hardware: dsBridge.call("lyh5x.device.hardware"),
		idfa: dsBridge.call("lyh5x.device.idfa"),
		idfv: dsBridge.call("lyh5x.device.idfv"),
		imsi: dsBridge.call("lyh5x.device.imsi"),
		ip: dsBridge.call("lyh5x.device.ip"),
		language: dsBridge.call("lyh5x.device.language"),
		manufacturer: dsBridge.call("lyh5x.device.manufacturer"),
		mno: dsBridge.call("lyh5x.device.mno"),
		model: dsBridge.call("lyh5x.device.model"),
		operatorType: dsBridge.call("lyh5x.device.operatorType"),
		osType: dsBridge.call("lyh5x.device.osType"),
		osVersion: dsBridge.call("lyh5x.device.osVersion"),
		packageName: dsBridge.call("lyh5x.device.packageName"),
		pushVersion: dsBridge.call("lyh5x.device.pushVersion"),
		screenBrightness: dsBridge.call("lyh5x.device.screenBrightness"),
		screenDensity: dsBridge.call("lyh5x.device.screenDensity"),
		screenHeight: dsBridge.call("lyh5x.device.screenHeight"),
		screenSize: dsBridge.call("lyh5x.device.screenSize"),
		screenType: dsBridge.call("lyh5x.device.screenType"),
		screenWidth: dsBridge.call("lyh5x.device.screenWidth"),
		udid: dsBridge.call("lyh5x.device.udid"),
		usbState: dsBridge.call("lyh5x.device.usbState"),
		uuid: dsBridge.call("lyh5x.device.uuid"),
		vendor: dsBridge.call("lyh5x.device.vendor"),
	}
	export default {
		name:'payPhoneConfirm',
		data(){
			return {
				checked:true,
				payData:{},
				phone:'',
				cashBack:'',
				discount:''
			}
		},
		mounted() {
			let _this = this;
			console.log(_this.$route.query);
			_this.payData = _this.$route.query.itemData;
			_this.phone = _this.$route.query.mobile;
			_this.cashBack = _this.$route.query.cashBack;
			_this.discount = _this.$route.query.discount;
			var jsonObj = localStorage.getItem("obj");
			_this.obj = JSON.parse(jsonObj);
		},
		methods:{
			goBack(){
			  this.$router.back();
			},
			pay(){
				let _this = this;
				if(_this.checked){
					dataSS.mobile = _this.phone;
					dataSS.actualAmount = _this.payData.amount;
					dataSS.payAmount = _this.payData.payAmount;
					dataSS.cardId = _this.$store.state.bond.id;
					dataSS.imgUrl = _this.payData.imgUrl;
					var requestData = Object.assign(_this.obj,dataSS);
					console.log(requestData);
					var data = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
					_this.$axios({
					  method: 'post',
					  url: '/recharge/pay',
					  data:data,
					}).then(function(res) {
					  console.log(res);
					  if(res.data.code === 200){
						  var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
						  console.log(reData);
						  if(reData.type == true){
							  _this.$toast("充值成功");
							  _this.rechargeGetInfo(reData.orderNo);
						  }else{
							dsBridge.call("lyh5x.wxapi.registerApp", { wxAppId: reData.wxAppId, universalLink: reData.universalLink })
							dsBridge.call("lyh5x.wxapi.pay", { prepayId: reData.prepayId, mchId: reData.mchId, packageStr: reData.packageStr, noncestr: reData.noncestr, timestamp: reData.timestamp, sign: reData.sign, universalLink: reData.universalLink }, function (v) {
								console.log('支付===',v);
								if (v === "success") {
									_this.rechargeGetInfo(reData.orderNo);
								} else {
									_this.$toast("支付失败");
								}
							})
						  }
						
					  }else{
						  _this.$toast(res.data.msg);
					  }
					});
				}
			},
			rechargeGetInfo(orderNo){
				let _this = this;
				var obj = {
					amount:0,
					id:''
				}
				_this.$store.commit('selectBond',obj);
				var exclusiveObj = {
					orderNo:orderNo
				};
				var requestData = Object.assign(_this.obj,exclusiveObj);
				console.log(requestData);
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
				  method: 'post',
				  url: '/recharge/getInfo',
				  data:redata,
				}).then(function(res) {
				  console.log(res);
				  if(res.data.code === 200){
					var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
					console.log(reData);
					_this.goPhoneBillDetails(reData);
				  }
				});
			},
			goPhoneBillDetails(item){
				this.$router.push({path:'/phoneBillDetails',query:{item:item}})
			},
			selectBond(){
				this.$router.push({path:'/myCoupons',query:{type:1}});
			}
		}
	}
</script>

<style scoped>
	.payPhoneConfirm{
		width: 10.8rem;
		min-height: 100vh;
		background: #fff;
	}
	.payPhoneConfirm_head{
		width: 100%;
		height:2.32rem;
		box-sizing: border-box;
		padding-top:var(--barHeight);
		padding-left:0.33rem;
		padding-right: 0.33rem;
		background: #fff;
		position: relative;
		border-bottom: 0.02rem solid #e1e1e1;
	}
	.icon_arrow_left_black{
		display: inline-block;
		vertical-align: middle;
		width: 0.56rem;
		height:0.56rem;
		background-image: url(../static/img/icon_arrow_left_black.png);
		background-repeat: no-repeat;
		background-size: 100%;
		margin-top:0.47rem;
		position: relative;
		z-index: 5;
	}
	.payPhoneConfirm_head_title{
		width: 100%;
		height: 1.52rem;
		line-height: 1.52rem;
		position: absolute;
		left:0;
		bottom:0;
		font-size: 0.48rem;
		color:#000;
		text-align: center;
		z-index: 3;
	}
	.payPhoneConfirm_main{
		width: 100%;
		box-sizing: border-box;
		padding:0.33rem;
	}
	.payPhoneConfirm_box{
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 0.3rem;
		padding-bottom: 0.45rem;
		border-bottom: 0.02rem solid #d2d2d2;
	}
	.payPhoneConfirm_box div{
		width: 8rem;
	}
	.payPhoneConfirm_box_img{
		width: 1.8rem;
		height:1.8rem;
	}
	.payPhoneConfirm_box_text1{
		line-height: 0.7rem;
		width: 100%;
		color:#232426;
		font-size: 0.48rem;
	}
	.payPhoneConfirm_box_text2{
		line-height: 0.7rem;
		width: 100%;
		color:#0cd4d2;
		font-size: 0.48rem;
	}
	.payPhoneConfirm_box_text2 span{
		font-size: 0.72rem;
		color:#ff588c;
	}
	.payPhoneConfirm_box_text4 span{
		display: inline-block;
		vertical-align: middle;
	}
	.icon_wechat{
		display: inline-block;
		vertical-align: middle;
		width: 0.66rem;
		height: 0.54rem;
		background-image: url(../static/img/icon_wechat.png);
		background-repeat: no-repeat;
		background-size: 100%;
		margin-right:0.2rem;
	}
	.payPhoneConfirm_box.flex{
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding:0 0.33rem;
		line-height: 0.8rem;
		font-size: 0.48rem;
		color:#232426;
		border-bottom: none;
		margin:0;
	}
	.payPhoneConfirm_box_text3{
		color:#848383;
	}
	.btn_pay{
		width: 100%;
		height:1.2rem;
		background-color: #ff588c;
		border-radius: 0.6rem;
		color:#fff;
		text-align: center;
		line-height: 1.2rem;
		font-size: 0.48rem;
		margin-top:0.6rem;
		margin-bottom:0.5rem;
	}
	.checkedText{
		font-size: 0.36rem;
		color:#232426;
	}
	.description{
		font-size: 0.3rem;
		color:#9e9e9e;
		line-height: 0.45rem;
		margin-top: 0.4rem;
	}
</style>
